'use client';

import { GameContainer } from "@/components/game/GameContainer";
import { Header } from "@/components/game/Header";
import { Card, CardContent } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Progress } from "@/components/ui/progress";
import { Heart, Droplets, Shield, Folder, Trash2, ArrowRightCircle, Flag, Loader2 } from "lucide-react";
import Image from "next/image";
import Link from "next/link";
import { useSearchParams } from "next/navigation";
import { useDoc, useFirestore, useUser, useMemoFirebase } from "@/firebase";
import { doc } from "firebase/firestore";
import { useEffect, useState } from "react";
import { generateNpc, NpcInput } from "@/ai/flows/ai-dungeon-master-npc";
import { generateArt } from "@/ai/flows/generate-art";

const Enemy = ({ name, hp, maxHp, image, dialogue }: { name: string; hp: number; maxHp: number; image: string, dialogue: string }) => (
    <div className="flex flex-col items-center gap-2">
        <Image src={image} alt={name} width={150} height={150} className="rounded-lg border-2 border-destructive" />
        <div className="text-center">
            <p className="font-bold">{name}</p>
            <p className="text-sm text-muted-foreground">HP: {hp}/{maxHp}</p>
            <Progress value={(hp/maxHp) * 100} className="w-32 h-2 bg-destructive/30" />
            <p className="text-xs italic text-amber-300/70 mt-2">&quot;{dialogue}&quot;</p>
        </div>
    </div>
);

const PlayerCard = ({ name }: { name: string }) => (
    <Card className="w-28 h-40 bg-secondary flex flex-col items-center justify-center cursor-pointer hover:border-primary hover:-translate-y-2 transition-transform duration-200">
        <CardContent className="p-2 text-center">
            <p className="font-bold text-sm">{name}</p>
        </CardContent>
    </Card>
);

export default function BattlePage() {
    const searchParams = useSearchParams();
    const characterId = searchParams.get('characterId');
    const firestore = useFirestore();
    const { user } = useUser();
    
    const [enemyNpc, setEnemyNpc] = useState<any>(null);
    const [enemyArt, setEnemyArt] = useState<string>('');
    const [isLoading, setIsLoading] = useState(true);

    const characterRef = useMemoFirebase(() => {
        if (!user || !firestore || !characterId) return null;
        return doc(firestore, 'users', user.uid, 'characters', characterId);
      }, [firestore, user, characterId]);
    
    const { data: character } = useDoc(characterRef);

    useEffect(() => {
        if (!character) return;

        const fetchEnemy = async () => {
            setIsLoading(true);
            const npcInput: NpcInput = {
                location: "battle",
                playerClass: character.class,
                playerContext: {
                    level: character.level,
                    choices: [],
                    reputation: { stealth: 0, combat: 0, diplomacy: 0 },
                    questFlags: {}
                }
            };
            const npc = await generateNpc(npcInput);
            setEnemyNpc(npc);

            const { photoDataUri } = await generateArt({
                subject: `A fantasy game enemy for a game called Poot Quest. The enemy is a ${npc.name}`,
                style: "Gritty fantasy oil painting"
            });
            setEnemyArt(photoDataUri);
            setIsLoading(false);
        }
        fetchEnemy();

    }, [character]);

  return (
    <>
      <Header />
      <main className="py-12">
        <GameContainer>
            <Card className="bg-card/50">
                <CardContent className="p-4 space-y-4">
                    {/* Enemy Area */}
                    <div className="flex justify-around items-end p-4 min-h-64 bg-muted/50 rounded-lg border-b-4 border-b-accent">
                        {isLoading || !enemyNpc ? (
                             <div className="flex flex-col items-center justify-center text-center">
                                <Loader2 className="h-16 w-16 animate-spin text-primary mb-4" />
                                <p className="text-xl text-muted-foreground">
                                Summoning a worthy foe...
                                </p>
                            </div>
                        ) : (
                            <Enemy name={enemyNpc.name} hp={30} maxHp={30} image={enemyArt} dialogue={enemyNpc.dialogue} />
                        )}
                    </div>

                    {/* Battlefield Zone */}
                    <div className="min-h-48 flex items-center justify-center text-muted-foreground italic">
                        Battlefield Zone
                    </div>

                    {/* Player Area */}
                    <div className="pt-4 border-t-2 border-border">
                        <div className="flex justify-between items-center mb-4 px-4">
                            <div className="flex items-center gap-4">
                                <div className="flex items-center gap-1 font-bold text-lg"><Heart className="h-5 w-5 text-red-500" /> {character?.health || '??'}/100</div>
                                <div className="flex items-center gap-1 font-bold text-lg"><Droplets className="h-5 w-5 text-blue-500" /> {character?.mana || '??'}/50</div>
                                <div className="flex items-center gap-1 font-bold text-lg"><Shield className="h-5 w-5 text-gray-400" /> 0</div>
                            </div>
                            <div className="flex items-center gap-2">
                                <Button asChild variant="destructive" size="lg">
                                    <Link href="/">
                                        <Flag className="mr-2 h-4 w-4" /> Flee
                                    </Link>
                                </Button>
                                <Button size="lg">End Turn <ArrowRightCircle className="ml-2 h-4 w-4" /></Button>
                            </div>
                        </div>

                        <div className="flex flex-col items-center gap-4">
                            <div className="flex items-end gap-4">
                                <PlayerCard name="Backstab" />
                                <PlayerCard name="Silent But Deadly" />
                                <PlayerCard name="Smoke Bomb" />
                                <PlayerCard name="Basic Attack" />
                                <PlayerCard name="Basic Attack" />
                            </div>
                            <div className="flex gap-8 text-sm text-muted-foreground">
                                <div className="flex items-center gap-1"><Folder className="h-4 w-4" /> Deck: 12</div>
                                <div className="flex items-center gap-1"><Trash2 className="h-4 w-4" /> Discard: 3</div>
                            </div>
                        </div>
                    </div>
                </CardContent>
            </Card>
        </GameContainer>
      </main>
    </>
  );
}
