'use server';
/**
 * @fileOverview A Genkit flow for generating images.
 *
 * - generateArt - A function that generates an image based on a subject and style.
 * - GenerateArtInput - The input type for the generateArt function.
 * - GenerateArtOutput - The return type for the generateArt function.
 */

import { ai } from '@/ai/genkit';
import { z } from 'genkit';

const GenerateArtInputSchema = z.object({
  subject: z.string().describe('The subject of the image to generate.'),
  style: z
    .string()
    .describe('The artistic style of the image (e.g., oil painting, watercolor, pixel art).'),
});
export type GenerateArtInput = z.infer<typeof GenerateArtInputSchema>;

const GenerateArtOutputSchema = z.object({
  photoDataUri: z
    .string()
    .describe(
      "The generated image as a data URI, including MIME type and Base64 encoding. Format: 'data:<mimetype>;base64,<encoded_data>'."
    ),
});
export type GenerateArtOutput = z.infer<typeof GenerateArtOutputSchema>;

export async function generateArt(
  input: GenerateArtInput
): Promise<GenerateArtOutput> {
  return generateArtFlow(input);
}

const generateArtFlow = ai.defineFlow(
  {
    name: 'generateArtFlow',
    inputSchema: GenerateArtInputSchema,
    outputSchema: GenerateArtOutputSchema,
  },
  async ({ subject, style }) => {
    const { media } = await ai.generate({
      model: 'googleai/imagen-4.0-fast-generate-001',
      prompt: `Generate an image of ${subject} in the style of ${style}.`,
      config: {
        safetySettings: [
            {
                category: 'HARM_CATEGORY_DANGEROUS_CONTENT',
                threshold: 'BLOCK_NONE',
            },
            {
                category: 'HARM_CATEGORY_HATE_SPEECH',
                threshold: 'BLOCK_NONE',
            },
            {
                category: 'HARM_CATEGORY_HARASSMENT',
                threshold: 'BLOCK_NONE',
            },
            {
                category: 'HARM_CATEGORY_SEXUALLY_EXPLICIT',
                threshold: 'BLOCK_NONE',
            },
        ]
      }
    });

    if (!media.url) {
      throw new Error('Image generation failed to produce a data URI.');
    }

    return { photoDataUri: media.url };
  }
);
